export const DOTTED_STROKE: DottedLine = 'dotted stroke'
export const TERMINATE_WHEN:TerminateWhen = 'terminate?'
export const INCREMENT_REGISTER_1: Increment1 = 'increment1'
export const DECREMENT_REGISTER_1: Decrement1 = 'decrement1'
export const INCREMENT_REGISTER_2: Increment2 = 'increment2'
export const DECREMENT_REGISTER_2: Decrement2 = 'decrement2'
export const INCREMENT_REGISTER_3: Increment3 = 'increment3'
export const DECREMENT_REGISTER_3: Decrement3 = 'decrement3'

export const availableCommands: List = [
  AB,
  FOR,
  FILL,
  SPLIT,
  SHIFT,
  CROOK,
  MARGIN,
  STROKE,
  SWITCH,
  SQUARE,
  WINDOW,
  CIRCLE,
  LINES,
  MULTIPLY,
  TRIANGLE,
  SAME_PLACE, 
  TERMINATE_WHEN,
  INCREMENT_REGISTER_1,
  INCREMENT_REGISTER_2,
  INCREMENT_REGISTER_3,
  DECREMENT_REGISTER_1,
  DECREMENT_REGISTER_2,
  DECREMENT_REGISTER_3,
]

const chances = {
  [AB]: random(50),
  [FOR]: random(50),
  [FILL]: random(50),
  [SHIFT]: random(50),
  [SPLIT]: random(50),
  [CROOK]: random(50),
  [LINES]: random(30),
  [MARGIN]: random(50),
  [STROKE]: random(50),
  [SWITCH]: random(50),
  [CIRCLE]: random(25),
  [SQUARE]: random(50),
  [WINDOW]: random(3),
  [MULTIPLY]: random(50),
  [TRIANGLE]: random(2),
  [SAME_PLACE]: random(50),
  [TERMINATE_WHEN]: random(20),
  [INCREMENT_REGISTER_1]: random(50),
  [DECREMENT_REGISTER_1]: random(50),
  [INCREMENT_REGISTER_2]: random(50),
  [DECREMENT_REGISTER_2]: random(50),
  [INCREMENT_REGISTER_3]: random(50),
  [DECREMENT_REGISTER_3]: random(50),
}

const weightedCommands = availableCommands.reduce((result:List, current:CommandId):List => {
  const frequency:number = chances[current]
  for (let i=0; i < frequency; i++) {
    result.push(current)
  }
  return result
}, [])

const randomVariableAddress = ():CGPVariableAddress => random(DIMENSIONS.memory - 1)

const randomPalette = ():Palette => initArray(DIMENSIONS.colors, randomColorArray)

const randomNodeIndex = (layer:number):number => {
  const firstNode = (layer + 1) * DIMENSIONS.layerSize
  const result = Math.floor(Math.random() * DIMENSIONS.layerSize) + firstNode
  return result
}
  
function createRandomNode(layer:number):CGPNode {
  const command = sample(weightedCommands)
  if (!command) throw new Error("Invalid command")
  const last = layer === DIMENSIONS.layers - 1
  const randomNext = ():CGPNodeAddress => last ? null : randomNodeIndex(layer)

  return {
    command,
    next: [
      randomNext(),
      randomNext(),
      randomNext(),
    ],
    variables: [
      randomVariableAddress(),
      randomVariableAddress(),
      randomVariableAddress(),
    ],    
  }
}

