function createRandomMemory():Array<VariableValue> {
  const result = []
  for (let i = 0; i < DIMENSIONS.memory; i++) { 
    result[i] = random(MEMORY_VALUE_MAX) / 2 << 0
  }
  return result
}

export function createRandomGrid(): CGPGrid {
  const nodes: Array<CGPNode> = new Array()
  for (let layer = 0; layer < DIMENSIONS.layers; layer++) {
    for (let i = 0; i < DIMENSIONS.layerSize; i++) {      
      nodes[i + layer * DIMENSIONS.layerSize] = createRandomNode(layer)
    }
  }

  const memory = createRandomMemory()
  const palette = randomPalette()
  return { nodes, memory, palette }
}
