import { sleep } from './utils'
import { Viewport } from './utils'
import {
  AB,
  FOR,
  FILL,
  SPLIT,
  CROOK,
  SHIFT,
  LINES,
  SQUARE,
  WINDOW,
  STROKE,
  CIRCLE,
  MARGIN,
  SWITCH,
  TRIANGLE,
  MULTIPLY,
  SAME_PLACE,
  TERMINATE_WHEN,
  MEMORY_VALUE_MAX,
  INCREMENT_REGISTER_1,
  DECREMENT_REGISTER_1,
  INCREMENT_REGISTER_2,
  DECREMENT_REGISTER_2,
  INCREMENT_REGISTER_3,
  DECREMENT_REGISTER_3,

  CGPGrid,
  CGPNodeAddress,
  CGPNode,
  CGPVariableAddresses,
  CGPVariableAddress,
  VariableValues,
  VariableValue,
} from './CGP'

import { Color, Channel, ChannelAddress, toCSSRGB } from './color'
import { rect, circle, triangle, MARGIN_SPACE, MARGIN_LINE, rhomb } from './canvas'
import { bound } from './utils'
import CharacterNode from './CharacterNode';
export type BB = [number, number, number, number] // bounding box
export type State = { // basically used by a stack
  bb: BB,
}

type DirectionHorizontal = string
type DirectionVertical = string
type Direction = DirectionHorizontal | DirectionVertical

const VERTICAL:DirectionVertical = 'vertical'
const HORIZONTAL:DirectionHorizontal = 'horizontal'

const value = (variables:VariableValues, index:number, max:number):number => 
  Math.round(variables[index] / MEMORY_VALUE_MAX * max)

export default class Parser {
  aborted:boolean = false
  svg:any = null
  drawingSize:Viewport
  grid:CGPGrid
  onBeat: Function

  constructor(grid: CGPGrid, svg:any, drawingSize:Viewport, onBeat:Function) {
    this.grid = grid
    this.svg = svg
    this.drawingSize = drawingSize
    this.onBeat = onBeat
  }

  lookupValues(variables:CGPVariableAddresses):VariableValues {
    const { memory } = this.grid
    return variables.map((address:CGPVariableAddress):VariableValue => memory[address]) as VariableValues
  }

  incrementMemory(node:CGPNode, amount:number, registerIndex:number) {
    const { memory } = this.grid
    const oldValue = memory[node.variables[registerIndex]]
    const newValue = oldValue + amount * 4
    memory[node.variables[registerIndex]] = bound(newValue, 0, MEMORY_VALUE_MAX)
  }

  currentColor(node:CGPNode, grid:CGPGrid):Color {
    const { memory, palette } = grid
    const paletteIndex = Math.round(memory[node.variables[0]] / MEMORY_VALUE_MAX * (palette.length - 1))
    const colorPointer = palette[paletteIndex]
    const result = colorPointer.map((channelAddress:ChannelAddress):Channel => memory[channelAddress] / MEMORY_VALUE_MAX * 255)
    return result as Color
  }
  
  async decodeSplit(variables:VariableValues, state:State, node:CGPNode, takeFirst:boolean):Promise<void> {
    let direction:Direction | null = null
    if (variables[0] < MEMORY_VALUE_MAX / 3) {
      direction = HORIZONTAL
    } else if (variables[0] < MEMORY_VALUE_MAX / 3 * 2) {
      direction = VERTICAL
    } else {
      direction = state.bb[3] - state.bb[1] > state.bb[2] - state.bb[0] ?
        VERTICAL : HORIZONTAL
    }
    const count = value(variables, 1, 2) + 1
    console.log(count)
    const continueIndex = value(variables, 2, 3)
    const traverseOrder = variables[1] > MEMORY_VALUE_MAX / 2
    await this.split(direction, count, state, node, takeFirst, continueIndex, traverseOrder)
  }

  async split(
    direction: Direction,
    count:number,
    state:State,
    node:CGPNode,
    takeFirst:boolean,
    continueIndex:number | null,
    traverseOrder:boolean
  ) {
    const size = (direction === HORIZONTAL) ?
      (state.bb[2] - state.bb[0]) / count :
      (state.bb[3] - state.bb[1]) / count
  
    for (let i = 0; i < count; i++) {
      const index = traverseOrder ? i : count - i - 1
      const bb:BB = (direction === HORIZONTAL) ? [
        state.bb[0] + size * index, 
        state.bb[1], 
        state.bb[0] + size * (index + 1),
        state.bb[3]
      ] :
      [
        state.bb[0],
        state.bb[1] + size * index, 
        state.bb[2], 
        state.bb[1] + size * (index + 1),
      ]
  
      const newState:State = { ...state, bb }
  
      if (continueIndex !== null && index === continueIndex) {
        await this.drawNode(node.next[1], newState)
      } else {
        await this.drawNode(node.next[takeFirst ? 0 : index], newState)
      }
    }     
  }
  async drawWindow(state:State, node:CGPNode, takeCenter:boolean, gradientDirection:boolean, strokeOnly:boolean) {
    const centerBB:BB = rhomb(state.bb, this.currentColor(node, this.grid), this.svg, gradientDirection, strokeOnly)
    const nextState:State = { ...state, bb: takeCenter || true ? centerBB : state.bb }
    await this.drawNode(node.next[0], nextState)
  }

  drawLines(state:State, color:Color, variables:VariableValues) {
    const MAX_DISTANCE = 25
    const direction:Direction = variables[2] > variables[0] ? HORIZONTAL : VERTICAL
    const lineDistance = variables[1] / MEMORY_VALUE_MAX * MAX_DISTANCE << 0
    const { bb } = state
    const count = direction === VERTICAL ? 
      (bb[3] - bb[1]) / lineDistance << 0 : 
      (bb[2] - bb[0]) / lineDistance << 0

    for (let i = 0; i < count; i++) {
      let line = null
      if (direction === VERTICAL) {
        const y = bb[1] + (i+0.5) * lineDistance
        line = this.svg.line(
          bb[0],
          y,
          bb[2],
          y
        )
      } else {
        const x = bb[0] + (i+0.5) * lineDistance
        line = this.svg.line(
          x,
          bb[1],
          x,
          bb[3]
        )
      }

      line 
        .stroke({ width: 0.1 })
        .attr({ stroke: toCSSRGB(color) })
    }
  }

  async margin(margin:number, node:CGPNode, state:State):Promise<void> {
    const nextState:State = (
      state.bb[0] + margin < state.bb[2] - margin &&
      state.bb[1] + margin < state.bb[3] - margin
    ) ?
      {
        ...state,
        bb: [
          state.bb[0] + margin,
          state.bb[1] + margin,
          state.bb[2] - margin,
          state.bb[3] - margin
        ]
      }
    : state
  
    await this.drawNode(node.next[0], nextState)
  }

  async drawNode(index: CGPNodeAddress, state:State):Promise<void> {
    if (index === null || this.aborted) {
      return
    }
    
    await sleep(0)
    const { nodes } = this.grid
    const node = nodes[index]
  
    const { next, command } = node
    const variables = this.lookupValues(node.variables)
    
    this.onBeat(index, node, variables)
    switch (command) {
      case SAME_PLACE:
        await this.drawNode(next[0], state)
        await this.drawNode(next[1], state)
        break
  
      case SPLIT:
        await this.decodeSplit(variables, state, node, false)
        break
  
      case FOR:
        await this.decodeSplit(variables, state, node, true)
        break
  
      case SHIFT: {
        const scale = variables[1] / MEMORY_VALUE_MAX
        const move = variables[2] / MEMORY_VALUE_MAX
        const { bb } = state
  
        const width = (bb[2] - bb[0])
        const newWidth = width * scale
        const emptySpace = width - newWidth
        const offset = move * emptySpace
        
        const newState:State = {
          ...state,
          bb: [
            bb[0] + offset,
            bb[1],
            bb[0] + newWidth + offset,
            bb[3]
          ]
        } 
        await this.drawNode(next[0], newState)
        break
      }
  
      case AB: {
        const { bb } = state
        const width = (bb[2] - bb[0]) / 2
        const height = (bb[3] - bb[1]) / 2
  
        const direction = variables[1] > 127
        await this.drawNode(next[direction ? 0 : 1], { ...state, bb: [bb[0], bb[1], bb[0] + width, bb[1] + height] })
        await this.drawNode(next[direction ? 1 : 0], { ...state, bb: [bb[0] + width, bb[1], bb[0] + width * 2, bb[1] + height] })
        await this.drawNode(next[direction ? 1 : 0], { ...state, bb: [bb[0], bb[1] + height, bb[0] + width, bb[1] + height * 2] })
        await this.drawNode(next[direction ? 0 : 1], { ...state, bb: [bb[0] + width, bb[1] + height, bb[0] + width * 2, bb[1] + height * 2] })
  
        break
      }
  
      case SWITCH: {
        const firstLargest = variables[0] > variables[1] ? 0 : 1
        const firstLargestValue = variables[firstLargest]
        const nextIndex = variables[2] > firstLargestValue ? 2 : firstLargest
        await this.drawNode(next[nextIndex], state)
        break
      }
  
      case TERMINATE_WHEN:
        if (variables[0] > variables[1]) {
          await this.drawNode(next[1], state)
        }
        break
  
      case MARGIN:
        await this.margin(MARGIN_SPACE, node, state)
        break
  
      case MULTIPLY:
        // multiplyMemory(node, memory)
        await this.drawNode(next[0], state)
        break

      case INCREMENT_REGISTER_1:
        this.incrementMemory(node, 1, 0)
        await this.drawNode(next[0], state)
        break
  
      case DECREMENT_REGISTER_1:
        this.incrementMemory(node, -1, 0)
        await this.drawNode(next[0], state)
        break

      case INCREMENT_REGISTER_2:
        this.incrementMemory(node, 1, 1)
        await this.drawNode(next[0], state)
        break
  
      case DECREMENT_REGISTER_2:
        this.incrementMemory(node, -1, 1)
        await this.drawNode(next[0], state)
        break

      case INCREMENT_REGISTER_3:
        this.incrementMemory(node, 1, 2)
        await this.drawNode(next[0], state)
        break
  
      case DECREMENT_REGISTER_3:
      this.incrementMemory(node, -1, 2)
        await this.drawNode(next[0], state)
        break
  
      case LINES:
        this.drawLines(state, this.currentColor(node, this.grid), variables)
        await this.margin(MARGIN_LINE, node, state)
        break

      case FILL:
        rect(state, this.currentColor(node, this.grid), null, this.svg, null, variables[1] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
  
      case STROKE:
        rect(state, null, this.currentColor(node, this.grid), this.svg, null, variables[1] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
        
      case CIRCLE:
        circle(state, this.currentColor(node, this.grid), this.svg, false, variables[1] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
  
      case SQUARE:
        circle(state, this.currentColor(node, this.grid), this.svg, true, variables[1] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
  
      case CROOK:
        rect(state, this.currentColor(node, this.grid), null, this.svg, variables[0], variables[1] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
        
      case WINDOW:
        await this.drawWindow(
          state,
          node,
          variables[0] > MEMORY_VALUE_MAX / 2,
          variables[1] > MEMORY_VALUE_MAX / 2,
          variables[2] > MEMORY_VALUE_MAX / 2
        )
        break
        
      case TRIANGLE:
        triangle(state, this.currentColor(node, this.grid), this.svg, variables[1] > 127, variables[2] > MEMORY_VALUE_MAX / 2)
        await this.margin(MARGIN_LINE, node, state)
        break
  
      default:
        console.log(`not yet implemented: ${command}`)
    }
  }
  
  async draw():Promise<void> {
    if (this.aborted) return
    const initialState:State = {
      bb: [0, 0, this.drawingSize.width, this.drawingSize.height],
    }
    
    console.log('start...')
    await this.drawNode(0, initialState)
    CharacterNode.current = null
    console.log('done!')    
  }
  
  abort():void {
    this.aborted = true
  }  
}
