import { 
  CGPNodeAddress,
  CGPNode,
  VariableValues,
  DIMENSIONS,
  CGPGrid,
  MEMORY_VALUE_MAX,
} from './CGP'
import ColorBullet from './ColorBullet'
import { viewport } from './utils'
import { ChannelAddress, Channel, Color } from './color'
import CharacterNode, { CharacterGrid } from './CharacterNode'

const NODE_WIDTH = 48
const NODE_HEIGHT = 29

export default class Graph {
  private container: HTMLDivElement
  private grid: CGPGrid
  private characterGrid: CharacterGrid | null = null
  private palette: Array<ColorBullet> = []
  private intervalId: any

  constructor(container:HTMLDivElement, grid:CGPGrid) {
    this.container = container
    this.grid = grid
    this.draw()
    
    this.intervalId = setInterval(this.interval, 50)
  }

  release() {
    clearInterval(this.intervalId)

    while (this.container.firstChild) {
      this.container.removeChild(this.container.firstChild);
    }
  }

  updatePalette() {
    const { memory } = this.grid
    this.grid.palette.forEach((color, index) => {
      const result:Color = color.map((channelAddress:ChannelAddress):Channel => memory[channelAddress] / MEMORY_VALUE_MAX * 255) as Color
      this.palette[index].color = result
    })
  }

  createPalette() {
    const v = viewport()
    const result:Array<ColorBullet> = []
    const SPACING = 30
    
    const translate = (v.width - v.height - DIMENSIONS.colors * SPACING) / 2
    for (let i = 0; i < DIMENSIONS.colors; i++) {
      result[i] = new ColorBullet(i * SPACING + translate, this.container)
    }
    return result
  }

  draw() {
    const v = viewport()
    const dimensions = [
      NODE_WIDTH * DIMENSIONS.layerSize,
      NODE_HEIGHT * DIMENSIONS.layers
    ]
    const translate = [
      ((v.width - v.height) - dimensions[0]) /2,
      (v.height - dimensions[1]) /2,
    ]
    
    this.characterGrid = this.grid.nodes.reduce((result:CharacterGrid, node:CGPNode, index:number):CharacterGrid => {
      const x = index % DIMENSIONS.layerSize
      const y = Math.floor(index / DIMENSIONS.layerSize)

      if (y === 0 && x > 0) {
        return result
      }
      
      const left = index === 0
        ? dimensions[0] / 2 + translate[0] - NODE_WIDTH / 2
        : x * NODE_WIDTH + translate[0]

      const top = y * NODE_HEIGHT + translate[1]
      
      result[index] = new CharacterNode(left, top, this.container, node)
      return result
    }, [])    
    this.palette = this.createPalette()
  } 

  interval = () => {
    if (!this.characterGrid) return
    for (let i = 0; i < this.characterGrid.length; i++) {
      const characterNode:CharacterNode = this.characterGrid[i]
      if (!characterNode) continue
      characterNode.fade()
    }

    this.updatePalette()
  }

  update = (index: CGPNodeAddress, node:CGPNode, variables: VariableValues) => {
    if (index === null) return
    if (!this.characterGrid) return
    const characterNode:CharacterNode = this.characterGrid[index]
    characterNode.touch()
  }
}
