export default class CharacterNode {
  static current:CharacterNode | null
  private element:HTMLDivElement
  private traversed:boolean
  private _opacity:number = 0
  
  set opacity(value:number) {
    this._opacity = value
    this.element.style.opacity = `${this._opacity}`
  }

  fade() {
    if (CharacterNode.current === this) return
    const newOpacity = this._opacity * 0.9
    if (this.traversed) {
      this.opacity = Math.max(0.1, newOpacity)
    } else {
      this.opacity = Math.max(0, newOpacity)
    }
  }

  touch() {
    CharacterNode.current = this
    this.opacity = 1
    this.traversed = true
  }

  constructor(x:number, y:number, parent:HTMLDivElement, node:CGPNode) {
    this.traversed = false
    this.element = document.createElement('div')
    const character = CharacterMapping[node.command]
    if (character.length > 1) {
      this.element.innerHTML = `<img src=${character} />`
    } else {
      this.element.innerHTML = character
    }
    this.element.className = 'node'
    this.element.style.left = `${x}px`
    this.element.style.top = `${y}px`
    parent.appendChild(this.element)
    this.opacity = this._opacity
  }
}

export type CharacterGrid = Array<CharacterNode>
