import React, { Component, DOMElement } from 'react'
import SVG from 'svg.js'
import './App.css'
import { createRandomGrid, CGPGrid, mutate, DIMENSIONS } from './CGP'
import Parser from './Parser'
import { viewport, sleep } from './utils'
import Graph from './Graph'

export default class App extends Component {
  private containerElement: HTMLDivElement | null = null
  private graphContainer: HTMLDivElement | null = null
  private svg: any | null
  private grid: CGPGrid = createRandomGrid()
  private parser: Parser | null = null
  private graph: Graph | null = null

  shouldComponentUpdate() {
    return false
  }

  async makeDrawing(grid:CGPGrid) {
    const v = viewport()
    if (!this.grid) return
    const drawingSize = { width: v.height, height: v.height }
    this.svg.size(v.height, v.height)
    this.svg.clear()

    if (this.parser) {
      this.parser.abort()
    }

    if (!this.graph) throw new Error('Graph not initialised')
    this.parser = new Parser(grid, this.svg, drawingSize, this.graph.update)
    await this.parser.draw()
    await sleep(5000)
    this.refresh()
  }

  run = (containerElement:any) => {
    if (!containerElement || this.containerElement) return
    this.containerElement = containerElement
    this.svg = SVG(containerElement)
    this.makeDrawing(this.grid)
  }

  mutate():void {
    if (this.grid) {
      for (let i = 0; i < 10; i++) {
        mutate(this.grid)
      }
      this.makeDrawing(this.grid)
    }
  }

  refresh() {
    if (!this.graph) throw new Error("graph not initialzed")
    if (!this.graphContainer) throw new Error("graph container not defined");
    this.grid = createRandomGrid()
    this.graph.release()
    this.graph = new Graph(this.graphContainer, this.grid)
    this.makeDrawing(this.grid)
  }

  handleKey = (e:KeyboardEvent) => {
    switch (e.which) {
      case 77: // Mutate
        this.mutate()
        break

      case 83: // Save
        this.download()
        break
      case 82: // r
        this.refresh()
        break
      default:
        console.log(e.which)
    }
  }

  componentDidMount(){
    document.addEventListener("keydown", this.handleKey, false);
  }
  componentWillUnmount(){
    document.removeEventListener("keydown", this.handleKey, false);
  }
 
  download = () => {
    if (!this.containerElement) return
    const svgData = this.containerElement.innerHTML;
    const svgBlob = new Blob([svgData], {type:"image/svg+xml;charset=utf-8"});
    const svgUrl = URL.createObjectURL(svgBlob);
    const downloadLink = document.createElement("a");
    downloadLink.href = svgUrl;
    downloadLink.download = `cgp-${Date.now()}.svg`;
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  }

  initGraph = (container:HTMLDivElement) => {     
    if (this.graph) return
    this.graphContainer = container
    this.graph = new Graph(this.graphContainer, this.grid)
  }

  render() {
    return (
      <div className="App">
        <div id="network" ref={this.initGraph}></div>
        <div id="drawing" ref={this.run}></div>
      </div>
    )
  }
}
