import { DIMENSIONS } from './CGP'

export type ChannelAddress = number
export type Channel = number
export type Color = [Channel, Channel, Channel, Channel]
export type ColorPointer = [ChannelAddress, ChannelAddress, ChannelAddress, ChannelAddress]
export type Palette = Array<ColorPointer>
export const CHANNEL_ALPHA = 3

export const randomChannelAddress = ():Channel => Math.random() * (DIMENSIONS.memory - 1) << 0
export const randomColorArray = ():ColorPointer => [
  randomChannelAddress(),
  randomChannelAddress(),
  randomChannelAddress(),
  randomChannelAddress()
]

export const dim = (color:Color, d:number):Color => [color[0] * d, color[1] * d, color[2] * d, color[3]]
export const toCSSRGB = (rgbArray:Color) =>
  `rgb(${rgbArray[0] << 0},${rgbArray[1] << 0},${rgbArray[2] << 0})`

