export function dimmedGradient(color:Color, svg:any, direction:boolean):any {
  const dimmedColor:Color = dim(color, 0.85)
  return svg.gradient('linear', function(stop:any) {
    stop.at(0, direction ? toCSSRGB(dimmedColor) : toCSSRGB(color))
    stop.at(1, !direction ? toCSSRGB(dimmedColor) : toCSSRGB(color))
  }).from(0, 0).to(0, 1)
}
